<template>
  <div style="overflow:auto;padding:20px;" class='clear-fix' >
    <div style="margin-bottom:15px;display: flex;justify-content:flex-end;">
        <div class="select-wrapper">
            <DatePicker type="datetimerange" placement="bottom-end" v-model="page.date"  split-panels placeholder='请选择用户登录时间范围' style="width:350px"></DatePicker>
        </div>
        <div class="select-wrapper">
          <Select v-model="page.isvip" placeholder='请选择用户类型' style="width: 100px">
              <Option  :value="-1">全部</Option>
              <Option  :value="0">非会员</Option>
              <Option  :value="1">会员</Option>
          </Select>
        </div>
        <!-- <div class="select-wrapper">
          <Input v-model="page.nickname" placeholder="请输入用户昵称"></Input>
        </div> -->
        <!-- <div class="select-wrapper">
          <Input v-model="page.telnumber" placeholder="请输入手机号"></Input>
        </div> -->
        <div class="select-wrapper">
          <Button type="ghost"  @click='getMembers'>查询</Button>
        </div>
    </div>
    <Table :loading="loading" border  :columns="columns4" :data="memberdata"></Table>
    <div style="margin-top: 15px;overflow: hidden">
      <div style="float: left;"></div>
      <div style="float: right;">
        <Page :total="totalcount" :page-size="pagesize" :page-size-opts="[10,20,30,40]" show-elevator show-sizer
          placement="top" show-total @on-change='indexChange' @on-page-size-change='sizeChange'></Page>
      </div>
    </div>
    <Modal
        v-model="showBol"
        title="绑定关系"
        > 
        <div>
            <div>
                <Input v-model.trim='telnumber' placeholder="输入上级的手机号"  icon="ios-search" @on-click='lookMember'></Input>
            </div>
            <div style="font-size:16px;margin:20px 0 0 10px;position:relative;">
                <Spin size="large" fix v-if="spinShow"></Spin>
                <div  v-show='!!memberInfo.id'>
                    <img :src='memberInfo.avatarurl' style="width:100px;height:100px"/>
                    <div>
                        <div>
                            <span>姓名：</span>
                            <span>{{memberInfo.realname}}</span>
                        </div>
                        <div>
                            <span>昵称：</span>
                            <span>{{memberInfo.nickname}}</span>
                        </div>
                    </div>
                </div>
                <div v-show="memberInfo.id===''">暂无此用户</div>
            </div>
        </div>
        
        <div slot="footer">
            <Button @click='showBol=false'>取消</Button> 
            <Button type="primary" @click="submitConfirm">提交</Button>    
        </div>   
    </Modal>
  </div>
</template>
<script>
export default {
  data() {
    return {
      loading: false,
      pageindex: 0,
      pagesize: 10,
      page: {
        isvip: -1,
        date: ["", ""],
        nickname: "",
        // telnumber: ""
      },
      totalcount: 0,
      columns4: [
        {
          title: "#",
          width: 60,
          align: "center",
          render: (h, params) => {
            return params.index + this.pageindex * this.pagesize + 1;
          }
        },
        {
          title: "头像",
          width: 100,
          align: "center",
          render: (h, params) => {
            if (params.row.avatarurl) {
              return h("img", {
                attrs: {
                  src: params.row.avatarurl,
                  style:
                    "width: 70px;height: 70px;border-radius: 5px;vertical-align: middle;margin:5px 0;"
                }
              });
            } else {
              return h("div", {
                attrs: {
                  style:
                    "width: 70px;height: 70px;border:1px solid #666;margin:5px 0;vertical-align: middle;display:inline-block"
                }
              });
            }
          }
        },
        {
          title: "昵称",
          key: "nickname",
          align: "center",
          minWidth: 100
        },
        {
          title: "性别",
          key: "gender",
          align: "center",
          width: 80,
          render: (h, params) => {
            let text = "未知";
            switch (params.row.gender) {
              case '1':
                text = "男";
                break;
              case '2':
                text = "女";
                break;
            }
            return h("span", text);
          }
        },
        {
          title: "购物钱包",
          align: "center",
          key: "storecode",
          minWidth: 100,
          // render: (h, params) =>
          //   !!params.row.account ? params.row.account.storecode || "" : ""
        },
        {
          title: "奖金钱包",
          align: "center",
          key: "storename",
          minWidth: 100,
          // render: (h, params) =>
          //   !!params.row.account ? params.row.account.storename || "" : ""
        },
        {
          title: "积分",
          align: "center",
          key: "storecode",
          minWidth: 100,
          // render: (h, params) =>
          //   !!params.row.account ? params.row.account.idcard  || "" : ""
        },
        // {
        //   title: "奖章",
        //   minWidth: 100,
        //   align: "center",
        //   render: (h, params) =>
        //     !!params.row.account ? params.row.account.medal || "" : ""
        // },
        {
          title: "是否是VIP",
          key: "isvip",
          align: "center",
          minWidth: 120,
          render(h, params) {
            return params.row.isvip == 1 ? "是" : "否";
          }
        },
        // {
        //   title: "上级邀请人",
        //   key: "parentNickName",
        //   align: "center",
        //   minWidth: 120
        // },
        {
          title: "登录日期",
          key: "updatetime",
          width: 180,
          align: "center"
        },
        // {
        //   title: "会员注册日期",
        //   key: "registertime",
        //   width: 180,
        //   align: "center"
        // },
        {
          title: "操作",
          key: "action",
          width: 100,
          align: "center",
          render: (h, params) => {
            let lookBtn = h(
              "Button",
              {
                props: {
                  type: "primary",
                  size: "small"
                },
                on: {
                  click: () => {
                    this.$router.push({
                      path: "/member/detail",
                      query: { id: params.row.id }
                    });
                  }
                }
              },
              "查看详情"
            );
            let bundlingBtn = "";
            if (params.row.parentid === null) {
              bundlingBtn = h(
                "Button",
                {
                  props: {
                    size: "small"
                  },
                  style: {
                    marginTop: "5px"
                  },
                  on: {
                    click: () => {
                      this.showBol = true;
                      this.memid = params.row.id;
                      this.telnumber = "";
                      this.memberInfo = {
                        id: null,
                        nickname: "",
                        realname: ""
                      };
                    }
                  }
                },
                "绑定上级"
              );
            }
            return h("div", [lookBtn, bundlingBtn]);
          }
        }
      ],
      memberdata: [],
      showBol: false,
      telnumber: "",
      memberInfo: {
        id: null,
        nickname: "",
        realname: ""
      },
      spinShow: false
    };
  },
  mounted() {
    this.$sitemap.prvname = "用户管理";
    this.$sitemap.currname = "用户列表";
    this.$menuactive.open = ["MemberBlock"];
    this.$menuactive.active = "MemberList";
    this.getMembers();
  },
  activated() {
      console.log('activated');
  },
  methods: {
    formatGetForm() {
      let params = {};
      Object.keys(this.page).forEach(key => {
        switch (key) {
          case "date":
            if (!!this.page.date[0] && !!this.page.date[1]) {
              params.begin = Date.parse(this.page.date[0]) / 1000;
              params.end = Date.parse(this.page.date[1]) / 1000;
            }
            break;
          default:
            if (!!this.page[key] || `${this.page[key]}` === "0") {
              params[key] = this.page[key];
            }
            break;
        }
      });
      return {
        ...params,
        pageindex: this.pageindex,
        pagesize: this.pagesize
      };
    },
    //得到用户列表
    getMembers() {
      this.loading = true;
      this.$http
        .post(this.$api.member_list, this.formatGetForm())
        .then(res => {
          if (res.data.result == "1" && res.data.data) {
            this.memberdata = res.data.data;
            this.totalcount = +res.data.totalcount;
          } else {
            this.$Message.error(res.data.msg);
          }
        })
        .catch(erro => {
          this.$Message.error("服务器异常");
        })
        .then(() => {
          this.loading = false;
        });
    },
    indexChange(index) {
      this.pageindex = index - 1;
      this.getMembers();
    },
    sizeChange(size) {
      this.pagesize = size;
      this.getMembers();
    },
    //绑定关系
    lookMember() {
      this.spinShow = true;
      this.$http
        .post(this.$api.member_tempget, { telnumber: this.telnumber })
        .then(res => {
          if (!!res.data.result) {
            this.memberInfo = res.data.data || { id: "" };
          } else {
            this.$Message.error(res.data.msg);
          }
        })
        .catch(err => {
          this.$Message.error("服务器异常");
        })
        .then(ress => {
          this.spinShow = false;
        });
    },
    submitConfirm() {
      if (!this.memberInfo.id) {
        this.$Message.warning("请先查询手机号");
      } else {
        let params = {
          memid: this.memid,
          parentid: this.memberInfo.id
        };
        this.$http
          .post(this.$api.member_setparentid, params)
          .then(res => {
            if (res.data.code == "100000") {
              this.$Message.success("绑定成功！");
              this.getMembers();
            } else {
              this.$Message.error(res.data.msg);
            }
          })
          .catch(err => {
            this.$Message.error("服务器异常");
          })
          .then(ress => {
            this.showBol = false;
          });
      }
    }
  }
};
</script>
<style scoped>
.select-wrapper {
  display: inline-block;
  margin-right: 5px;
}
</style>



